            <div v-bind:class="btn0+ (btnSelected == '0'?' btnSelected':'')" @click="stakeBtnClick('0')" @mouseover="mouseOver('0')">0<div class="subBtn">{{showWin(this.btn0)}}</div></div>
            <div v-bind:class="btn1+ (btnSelected == '1'?' btnSelected':'')" @click="stakeBtnClick('1')" @mouseover="mouseOver('1')">1<div class="subBtn">{{showWin(this.btn1)}}</div></div>
            <div v-bind:class="btn2+ (btnSelected == '2'?' btnSelected':'')" @click="stakeBtnClick('2')" @mouseover="mouseOver('2')">2<div class="subBtn">{{showWin(this.btn2)}}</div></div>
            <div v-bind:class="btn3+ (btnSelected == '3'?' btnSelected':'')" @click="stakeBtnClick('3')" @mouseover="mouseOver('3')">3<div class="subBtn">{{showWin(this.btn3)}}</div></div>
          </div>
          <div class="rowButtons">
            <div v-bind:class="btn4+ (btnSelected == '4'?' btnSelected':'')" @click="stakeBtnClick('4')" @mouseover="mouseOver('4')">4<div class="subBtn">{{showWin(this.btn4)}}</div></div>
            <div v-bind:class="btn5+ (btnSelected == '5'?' btnSelected':'')" @click="stakeBtnClick('5')" @mouseover="mouseOver('5')">5<div class="subBtn">{{showWin(this.btn5)}}</div></div>
            <div v-bind:class="btn6+ (btnSelected == '6'?' btnSelected':'')" @click="stakeBtnClick('6')" @mouseover="mouseOver('6')">6<div class="subBtn">{{showWin(this.btn6)}}</div></div>
            <div v-bind:class="btn7+ (btnSelected == '7'?' btnSelected':'')" @click="stakeBtnClick('7')" @mouseover="mouseOver('7')">7<div class="subBtn">{{showWin(this.btn7)}}</div></div>
          </div>
          <div class="rowButtons">
            <div v-bind:class="btn8+ (btnSelected == '8'?' btnSelected':'')" @click="stakeBtnClick('8')" @mouseover="mouseOver('8')">8<div class="subBtn">{{showWin(this.btn8)}}</div></div>
            <div v-bind:class="btn9+ (btnSelected == '9'?' btnSelected':'')" @click="stakeBtnClick('9')" @mouseover="mouseOver('9')">9<div class="subBtn">{{showWin(this.btn9)}}</div></div>
            <div v-bind:class="btnA+ (btnSelected == 'A'?' btnSelected':'')" @click="stakeBtnClick('A')" @mouseover="mouseOver('A')">A<div class="subBtn">{{showWin(this.btnA)}}</div></div>
            <div v-bind:class="btnB+ (btnSelected == 'B'?' btnSelected':'')" @click="stakeBtnClick('B')" @mouseover="mouseOver('B')">B<div class="subBtn">{{showWin(this.btnB)}}</div></div>
          </div>
          <div class="rowButtons">
            <div v-bind:class="btnC+ (btnSelected == 'C'?' btnSelected':'')" @click="stakeBtnClick('C')" @mouseover="mouseOver('C')">C<div class="subBtn">{{showWin(this.btnC)}}</div></div>
            <div v-bind:class="btnD+ (btnSelected == 'D'?' btnSelected':'')" @click="stakeBtnClick('D')" @mouseover="mouseOver('D')">D<div class="subBtn">{{showWin(this.btnD)}}</div></div>
