            <div v-bind:class="btnF+ (btnSelected == 'F'?' btnSelected':'')" @click="stakeBtnClick('F')" @mouseover="mouseOver('F')">F<div class="subBtn">{{showWin(this.btnF)}}</div></div>
          </div>
