          <header></header>
        </hashProof>
        <div class="hist__tabs">
          <div v-bind:class="allHistory ? 'hist__subdetail_head active' : 'hist__subdetail_head'" @click="setHistory('all')"><div class="hist__val">All stakes</div></div>
          <div v-bind:class="!allHistory ? 'hist__subdetail_head active' : 'hist__subdetail_head'" @click="setHistory('my')"><div class="hist__val">My stakes</div></div>
        </div>
        <div class="hist">
          <div class="hist__detail top">
            <div class="hist__subdetail"><div class="hist__head">Block #</div></div>
            <div class="hist__subdetail"><div class="hist__head">Prediction</div></div>
            <div class="hist__subdetail"><div class="hist__head">Actual</div></div>
            <div class="hist__subdetail"><div class="hist__head">Stake</div></div>
            <div class="hist__subdetail"><div class="hist__head">Win</div></div>
          </div>
          <div v-bind:class="award._Won > 0 ? 'hist__detail win' : 'hist__detail'" v-for="award in history" :key="award._betKeyStakeId" @click="getProof(award._BlockNumber)">
            <div class="hist__subdetail">
              <div class="hist__val">{{award._BlockNumber}}</div>
            </div>
            <div class="hist__subdetail">
              <div class="hist__val">{{award._Prediction}}</div>
            </div>
            <div class="hist__subdetail">
              <div class="hist__val">{{award._ActualTail}}</div>
            </div>
            <div class="hist__subdetail">
              <div class="hist__val">{{award._StakeValue / 1000000}}</div>
            </div>
            <div class="hist__subdetail">
              <div class="hist__val">{{award._Won / 1000000}}</div>
