<script>
// TODO: реализовать кнопки, связать их контрактом, подключить события
import ContractABI from '../.././build/contracts/riddlehash.json'
import { mapState } from 'vuex';
//import HelloWorld from "@/components/HelloWorld.vue";
import details from "@/components/details.vue";
import hashProof from "@/components/hashProof.vue";
import {} from '../util/getTronWeb.js'
export default {
  name: 'mainpage',
  props: {
    msg: String
  },
  data () {
    return {
    showDetails: false,
    showProof: false,
    btn0: "button",
    btn1: "button",
    btn2: "button",
    btn3: "button",
    btn4: "button",
    btn5: "button",
    btn6: "button",
    btn7: "button",
    btn8: "button",
    btn9: "button",
    btnA: "button",
    btnB: "button",
    btnC: "button",
    btnD: "button",
    btnE: "button",
    btnF: "button",
    allHistory: true,
    history: [],
    blockNumber: 0
    }
  },
  beforeCreate () {
    console.log('started mainpage.vue this.$store.dispatch("registerTronWeb")')
    this.$store.dispatch('registerTronWeb')
  },
  mounted(){
    this.setHistory('all')
  },
  computed:
    mapState({
      refString: 'refString',
      tronWeb: 'tronWeb',
      User_name: 'mainAddressShort',
      mainAddress: 'mainAddress',
      gameStakeNice: 'gameStakeNice',
      gameStake: 'gameStake',
      awardHistory: 'awardHistory',
      awardHistoryMy: 'awardHistoryMy',
      //history: 'awardHistory',
      //history: () => this.allHistory ? 'awardHistory' : 'awardHistoryMy',
      ContractAddress: 'riddleHashContractAddress',
      user_balance_trx: state => Math.trunc(state.trxBallanceNice).toLocaleString(),
      blocksHistory: 'blocksHistory',
      isWin: 'isWin',
      btnSelected: 'btnSelected'
    }),
  components: {
    //'Hello-World': HelloWorld,
    'Details': details,
    'hashProof': hashProof
  },
  methods: {
    getblockNumber(){
      //let res = {blockNumber: this.BlockNumber, longhash: '0xasdasdasdsdasd'}
      return({blockNumber: this.BlockNumber, longhash: '0xasdasdasdsdasd'});
    },
    getProof(_blockNumber){
      this.BlockNumber=_blockNumber;
      this.showProof = true;
    },
    setHistory(kind){
      this.allHistory = (kind == 'all' ? true : false);
      this.history = (kind == 'all' ? this.awardHistory : this.awardHistoryMy);      
      let refLink = (this.$route.query.ref === undefined ? "" : this.$route.query.ref);
      console.log("mounted ref 2 ", refLink);
      console.log("url", window.location.origin + window.location.pathname + '#/?ref=' + refLink);
    },
    stakeClick(_sign) {
      if (_sign == '+') {
        if ((this.gameStakeNice + 10) <= 100) {
          this.$store.dispatch('writeStake', this.gameStakeNice + 10)
        }
      } else {
        if ((this.gameStakeNice - 10) >= 10) {          
          this.$store.dispatch('writeStake', this.gameStakeNice - 10)
        }
      }
    },
    showWin(elem) {
      return (elem == "maxBtn" ? "400%" : (elem == "midBtn" ? "100%" : (elem == "minBtn" ? "50%" : " " )))
    }, 
    stakeBtnClick (btnData) {
      let refLink = (this.$route.query.ref === undefined ? "" : this.$route.query.ref);
      let stakeValue = this.gameStake;//10*10**6;      
      let StakeNumberDec = parseInt(btnData, 16);
      let Stakeblock = this.blocksHistory[this.blocksHistory.length - 1].Number;
      console.log("Make Stake", this.ContractAddress, Stakeblock, StakeNumberDec, this.mainAddress, stakeValue, refLink);
      let result = this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).stake(StakeNumberDec, refLink).send({from: this.mainAddress, callValue: stakeValue});
      console.log(btnData, StakeNumberDec, stakeValue, result);
      this.setHistory('my');
    },
    mouseOver: function (btnData) {
      this.btn0 = "button"
      this.btn1 = "button"
      this.btn2 = "button"
      this.btn3 = "button"
      this.btn4 = "button"
      this.btn5 = "button"
      this.btn6 = "button"
      this.btn7 = "button"
      this.btn8 = "button"
      this.btn9 = "button"
      this.btnA = "button"
      this.btnB = "button"
      this.btnC = "button"
      this.btnD = "button"
      this.btnE = "button"
      this.btnF = "button"
      if (btnData == '0') {
        this.btn0 = "maxBtn"
        this.btn1 = "midBtn"
        this.btn4 = "midBtn"
        this.btn5 = "minBtn"
      } else if (btnData == '1') {
        this.btn1 = "maxBtn"
        this.btn0 = "midBtn"
        this.btn2 = "midBtn"
        this.btn5 = "midBtn"
        this.btn4 = "minBtn"
        this.btn6 = "minBtn"
      } else if (btnData == '2') {
        this.btn2 = "maxBtn"
        this.btn1 = "midBtn"
        this.btn3 = "midBtn"
        this.btn6 = "midBtn"
        this.btn5 = "minBtn"
        this.btn7 = "minBtn"
      } else if (btnData == '3') {
        this.btn3 = "maxBtn"
        this.btn2 = "midBtn"
        this.btn7 = "midBtn"
        this.btn6 = "minBtn"
      } else if (btnData == '4') {
        this.btn4 = "maxBtn"
        this.btn0 = "midBtn"
        this.btn5 = "midBtn"
        this.btn8 = "midBtn"
        this.btn1 = "minBtn"
        this.btn9 = "minBtn"
      } else if (btnData == '5') {
        this.btn5 = "maxBtn"
        this.btn1 = "midBtn"
        this.btn4 = "midBtn"
        this.btn6 = "midBtn"
        this.btn9 = "midBtn"
        this.btn0 = "minBtn"
        this.btn2 = "minBtn"
        this.btn8 = "minBtn"
        this.btnA = "minBtn"
      } else if (btnData == '6') {
        this.btn6 = "maxBtn"
        this.btn2 = "midBtn"
        this.btn5 = "midBtn"
        this.btn7 = "midBtn"
        this.btnA = "midBtn"
        this.btn1 = "minBtn"
        this.btn3 = "minBtn"
        this.btn9 = "minBtn"
        this.btnB = "minBtn"
      } else if (btnData == '7') {
        this.btn7 = "maxBtn"
        this.btn3 = "midBtn"
        this.btn6 = "midBtn"
        this.btnB = "midBtn"
        this.btn2 = "minBtn"
        this.btnA = "minBtn"
      } else if (btnData == '8') {
        this.btn8 = "maxBtn"
        this.btn4 = "midBtn"
        this.btn9 = "midBtn"
        this.btnC = "midBtn"
        this.btn5 = "minBtn"
        this.btnD = "minBtn"
      } else if (btnData == '9') {
        this.btn9 = "maxBtn"
        this.btn5 = "midBtn"
        this.btn8 = "midBtn"
        this.btnA = "midBtn"
        this.btnD = "midBtn"
        this.btn4 = "minBtn"
        this.btn6 = "minBtn"
        this.btnC = "minBtn"
        this.btnE = "minBtn"
      } else if (btnData == 'A') {
        this.btnA = "maxBtn"
        this.btn6 = "midBtn"
        this.btn9 = "midBtn"
        this.btnB = "midBtn"
        this.btnE = "midBtn"
        this.btn5 = "minBtn"
        this.btn7 = "minBtn"
        this.btnD = "minBtn"
        this.btnF = "minBtn"
      } else if (btnData == 'B') {
        this.btnB = "maxBtn"
        this.btn7 = "midBtn"
        this.btnA = "midBtn"
        this.btnF = "midBtn"
        this.btn6 = "minBtn"
        this.btnE = "minBtn"
      } else if (btnData == 'C') {
        this.btnC = "maxBtn"
        this.btn8 = "midBtn"
        this.btnD = "midBtn"
        this.btn9 = "minBtn"
      } else if (btnData == 'D') {
        this.btnD = "maxBtn"
        this.btn9 = "midBtn"
        this.btnC = "midBtn"
        this.btnE = "midBtn"
        this.btn8 = "minBtn"
        this.btnA = "minBtn"
      } else if (btnData == 'E') {
        this.btnE = "maxBtn"
        this.btnA = "midBtn"
        this.btnD = "midBtn"
        this.btnF = "midBtn"
        this.btn9 = "minBtn"
        this.btnB = "minBtn"
      } else if (btnData == 'F') {
        this.btnF = "maxBtn"
        this.btnB = "midBtn"
        this.btnE = "midBtn"
        this.btnA = "minBtn"
      } else {
        this.btn0 = "button";
        this.btn1 = "button";
        this.btn2 = "button";
        this.btn3 = "button";
        this.btn4 = "button";
        this.btn5 = "button";
        this.btn6 = "button";
        this.btn7 = "button";
        this.btn8 = "button";
        this.btn9 = "button";
        this.btnA = "button";
        this.btnB = "button";
        this.btnC = "button";
        this.btnD = "button";
        this.btnE = "button";
        this.btnF = "button";
      }
    }
  }

}
</script>