<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <div class="modal-header">
                        <div class="head1">Mining round {{roundNumber}}</div>
                        <div class="progress" :data-label="percentDoneText">
                            <span class="value" :style="percentDoneVal"></span>
                        </div>
                        <div class="head4">Play {{ratio}} TRX to mine 1 HASH </div>
                        <div class="head3">HASHDrops <font class="mark">{{HASHDropBalance}} TRX</font></div>
                        <div class="head3">Total frozen <font class="mark">{{TotalFrozenPLAY}} HASH</font></div> 
                        <div class="head3">You will receive <font class="mark">{{personalReward}} TRX</font></div>
                    </div>
                    <div class="modal-body">
                        <div class="modal-body col">
                            <div class="col-head">HASH balance</div>
                            <div class="col-data">{{PLAYbalance}}</div>
                        </div>
                        <div class="modal-body col">
                            <div class="col-head">HASH deposited</div>
                            <div class="col-data">{{PLAYdepo}}</div>
                        </div>
                        <div class="modal-body col">
                            <div class="col-head">HASH frozen</div>
                            <div class="col-data">{{frozenPLAY}}</div>
                        </div>
                    </div>
                    <div class="modal-body">
                        <div class="dapp-btn" @click="showDeposite = true">Deposite</div>
                            <inputForm v-if="showDeposite" @close="showDeposite = false">
                                <template v-slot:body>
                                    <div class="modal-body close">
                                        <div class="input-head">HASH balance</div>
                                        <div class="input-value"> {{PLAYbalance}}</div>
                                    </div>
                                    <div class="modal-body close">
                                        <input v-model="value" @input="onlyNumbers">
                                        <div class="dapp-btn" @click="all(PLAYbalance)">All</div>
                                    </div>
                                </template>
                                <template v-slot:dapp-btn>
                                    <div v-bind:class="btnDeposite" @click="deposite()">Deposite</div>
                                </template>
                                <template v-slot:footer>*Deposite any HASH value.</template>
                            </inputForm>
                        <div class="dapp-btn" @click="showWithdraw = true">Withdraw</div>
                            <inputForm v-if="showWithdraw" @close="showWithdraw = false">
                                <template v-slot:body>
                                    <div class="modal-body close">
                                        <div class="input-head">HASH deposited </div>
                                        <div class="input-value"> {{PLAYdepo}}</div>
                                    </div>
                                    <div class="modal-body close">
                                        <input v-model="value" @input="onlyNumbers">
                                        <div class="dapp-btn" @click="all(PLAYdepo)">All</div>
                                    </div>
                                </template>
                                <template v-slot:dapp-btn>
                                    <div v-bind:class="btnWithdraw" @click="withdraw()">Withdraw</div>
                                </template>
                                <template v-slot:footer>*Withdraw any HASH value.</template>
                            </inputForm>
                        <div class="dapp-btn" @click="showFreeze = true">Freeze</div>
                            <inputForm v-if="showFreeze" @close="showFreeze = false">
                                <template v-slot:body>
                                    <div class="modal-body close">
                                        <div class="input-head">HASH deposited </div>
                                        <div class="input-value"> {{PLAYdepo}}</div>
                                    </div>
                                    <div class="modal-body close">
                                        <input v-model="value" @input="onlyNumbers">
                                        <div class="dapp-btn" @click="all(PLAYdepo)">All</div>
                                    </div>
                                </template>
                                <template v-slot:dapp-btn>
                                    <div v-bind:class="btnFreeze" @click="freeze()">Freeze</div>
                                </template>
                                <template v-slot:footer>*Freeze any HASH value.</template>
                            </inputForm>
                        <div class="dapp-btn" @click="showUnFreeze = true">Unfreeze</div>
                            <inputForm v-if="showUnFreeze" @close="showUnFreeze = false">
                                <template v-slot:body>
                                    <div class="modal-body close">
                                        <div class="input-head">HASH frozen </div>
                                        <div class="input-value"> {{frozenPLAY}}</div>
                                    </div>
                                    <div class="modal-body close">
                                        <input v-model="value" @input="onlyNumbers">
                                        <div class="dapp-btn" @click="all(frozenPLAY)">All</div>
                                    </div>
                                </template>
                                <template v-slot:dapp-btn>
                                    <div v-bind:class="btnUnFreeze" @click="unfreeze()">Unfreeze</div>
                                </template>
                                <template v-slot:footer>*Unfreeze any HASH value.</template>
                            </inputForm>
                    </div>
                    <div v-if="showOS">
                        <div class="modal-body">
                            <div class="modal-body col">
                                <div class="col-head">OS balance</div>
                                <div class="col-data">{{OSbalance}}</div>
                            </div>
                            <div class="modal-body col">
                                <div class="col-head">OS deposited</div>
                                <div class="col-data">{{OSdepo}}</div>
                            </div>
                            <div class="modal-body col">
                                <div class="col-head">OS frozen</div>
                                <div class="col-data">{{frozenOS}}</div>
                            </div>
                        </div>
                        <div class="modal-body">
                            <div class="dapp-btn" @click="showDepositeOS = true">Deposite</div>
                                <inputForm v-if="showDepositeOS" @close="showDepositeOS = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">OS balance</div>
                                            <div class="input-value"> {{OSbalance}}</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="value" @input="onlyNumbers">
                                            <div class="dapp-btn" @click="all(OSbalance)">All</div>
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnDepositeOS" @click="depositeOS()">Deposite</div>
                                    </template>
                                    <template v-slot:footer>*Deposite any OS value.</template>
                                </inputForm>
                            <div class="dapp-btn" @click="showWithdrawOS = true">Withdraw</div>
                                <inputForm v-if="showWithdrawOS" @close="showWithdrawOS = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">OS deposited </div>
                                            <div class="input-value"> {{OSdepo}}</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="value" @input="onlyNumbers">
                                            <div class="dapp-btn" @click="all(OSdepo)">All</div>
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnWithdrawOS" @click="withdrawOS()">Withdraw</div>
                                    </template>
                                    <template v-slot:footer>*Withdraw any OS value.</template>
                                </inputForm>
                            <div class="dapp-btn" @click="showFreezeOS = true">Freeze</div>
                                <inputForm v-if="showFreezeOS" @close="showFreezeOS = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">OS deposited </div>
                                            <div class="input-value"> {{OSdepo}}</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="value" @input="onlyNumbers">
                                            <div class="dapp-btn" @click="all(OSdepo)">All</div>
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnFreezeOS" @click="freezeOS()">Freeze</div>
                                    </template>
                                    <template v-slot:footer>*Freeze any OS value.</template>
                                </inputForm>
                            <div class="dapp-btn" @click="showUnFreezeOS = true">Unfreeze</div>
                                <inputForm v-if="showUnFreezeOS" @close="showUnFreezeOS = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">OS frozen </div>
                                            <div class="input-value"> {{frozenOS}}</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="value" @input="onlyNumbers">
                                            <div class="dapp-btn" @click="all(frozenOS)">All</div>
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnUnFreezeOS" @click="unfreezeOS()">Unfreeze</div>
                                    </template>
                                    <template v-slot:footer>*Unfreeze any OS value.</template>
                                </inputForm>
                        </div>
                    </div>
                    <div class="modal-header">
                        <div class="head3">Ref percent from stake <font class="mark">{{refPercent}} %</font></div>
                        <div class="head3">Ref link
                            <div class="modal-body">
                                <font class="mark-small">
                                    <input type="hidden" id="idRefFullLink" :value="RefFullLink">
                                    <div id="_idRefFullLink">{{RefFullLink}}</div>
                                </font>
                                <div v-bind:class=" RefFullLink === 'YOUR REF LINK WILL BE HERE' ? 'hide' : 'copy-btn'" @click="copyToClipboard()">Copy</div> <!-- v-bind:class="copy-btn" -->
                            </div>
                        </div>
                        <div class="head3">Ref rewards <font class="mark">{{RefBallance}} TRX</font></div>
                        <div class="modal-body">
                            <div class="dapp-btn" @click="showSetRefLink = true">Set Ref link</div>
                                <inputForm v-if="showSetRefLink" @close="showSetRefLink = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">Referral link</div>
                                            <div class="input-value"> {{RefAlias}}</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="valueRefString" @input="onlyNumbersLetters">
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnSetRefAlias" @click="setRefAlias()">Set alias</div>
                                    </template>
                                    <template v-slot:footer>*Set referral link value.</template>
                                </inputForm>
                            <div class="dapp-btn" @click="showWithdrawRefRewards = true">Withdraw ref rewards</div>
                                <inputForm v-if="showWithdrawRefRewards" @close="showWithdrawRefRewards = false">
                                    <template v-slot:body>
                                        <div class="modal-body close">
                                            <div class="input-head">Your referral rewards</div>
                                            <div class="input-value"> {{RefBallance}} TRX</div>
                                        </div>
                                        <div class="modal-body close">
                                            <input v-model="value" @input="onlyNumbers">
                                            <div class="dapp-btn" @click="all(RefBallance)">All</div>
                                        </div>
                                    </template>
                                    <template v-slot:dapp-btn>
                                        <div v-bind:class="btnWithdrawRef" @click="withdrawRef()">Withdraw rewards</div>
                                    </template>
                                    <template v-slot:footer>*Withdraw your rewards.</template>
                                </inputForm>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <slot name="footer">
                            *Minimum 1 HASH to freeze or unfreeze.
                            <button class="modal-default-button" @click="$emit('close')">Close</button>
                        </slot>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapState } from 'vuex';
    import ContractABI from '../.././build/contracts/riddlehash.json'
    import HASHTokenABI from '../.././build/contracts/HASH.json'
    import OSTokenABI from '../.././build/contracts/ROS.json'
    import inputForm from "@/components/inputForm.vue";
    import {getAccountTokensData, GetBallance, PoolBalance, frozenPLAY, frozenOS, PLAYTokenBalance, OSTokenBalance, refPercent, 
        getRefBallance, getRefAddressAlias} from '../util/getTronWeb.js'
    import BigNumber from "bignumber.js";
    export default {
        data () {
            return {
                showWithdraw: false,
                showDeposite: false,
                showFreeze:   false,
                showUnFreeze: false,
                showWithdrawOS: false,
                showDepositeOS: false,
                showFreezeOS:   false,
                showUnFreezeOS: false,
                showSetRefLink: false,
                showWithdrawRefRewards: false,
                value: 1,
                valueRefString: 'ref_',
                btnWithdrawRef: "dapp-btn",
                btnSetRefAlias: "dapp-btn",
                btnDeposite: "dapp-btn",
                btnWithdraw: "dapp-btn",
                btnFreeze: "dapp-btn",
                btnUnFreeze: "dapp-btn",
                btnDepositeOS: "dapp-btn",
                btnWithdrawOS: "dapp-btn",
                btnFreezeOS: "dapp-btn",
                btnUnFreezeOS: "dapp-btn"
            }
        },
        computed:
        mapState({
            roundNumber: 'roundNumber',
            ratio: 'ratio',
            roundFrom: 'roundFrom',
            roundTo: 'roundTo',
            sumTotal: 'sumTotal',
            percentDoneText: state => "Played " + state.sumTotal + " / " + state.roundTo + " TRX",
            percentDoneVal: state => "width:" + Math.ceil10(state.sumTotal / state.roundTo * 100, 0) + "%;",
            showOS: state => ((state.OSdepo > 0 || state.OSbalance > 0 || state.frozenOS > 0) ? true : false),
            frozenPLAY: 'frozenPLAY', 
            frozenOS: 'frozenOS',
            PLAYdepo: 'PLAYdepo',
            OSdepo: 'OSdepo',
            frozenBlock: 'frozenBlock',
            HASHDropBalance: 'HASHDropBalance',
            TotalFrozenPLAY: 'TotalFrozenPLAY',
            personalReward: state => {
                let a = Math.round10(state.frozenPLAY / state.TotalFrozenPLAY * state.HASHDropBalance, -2)
                return (isNaN(a) ? 0 : a )
            },
            PLAYbalance: 'PLAYbalance',
            ContractAddress: 'riddleHashContractAddress',
            HashTokenAddress: 'HashTokenContractAddress',
            OSTokenAddress: 'OSTokenContractAddress',
            OSbalance: 'OSbalance',
            tronWeb: 'tronWeb',
            refPercent: 'refPercent',
            RefBallance: 'RefBallance',
            RefAlias: 'RefAlias',
            RefFullLink: state => (state.RefAlias == '' ? 'YOUR REF LINK WILL BE HERE' : window.location.origin + window.location.pathname + '#/?ref=' + state.RefAlias)
        }),
        components: {
            'inputForm' : inputForm
        },
        mounted: function(){
            getAccountTokensData()
            PoolBalance()
            frozenPLAY()
            frozenOS()
            PLAYTokenBalance()
            OSTokenBalance()
            refPercent()
            getRefBallance()
            getRefAddressAlias()
        },
        methods:{
            copyToClipboard: function() {
                let testingCodeToCopy = document.querySelector('#idRefFullLink')
                testingCodeToCopy.setAttribute('type', 'text')    // 不是 hidden 才能複製
                testingCodeToCopy.select()
                                

                /* Copy the text inside the text field */
                document.execCommand("copy");
                //idRefFullLink
                /* unselect the range */
                testingCodeToCopy.setAttribute('type', 'hidden')
                window.getSelection().removeAllRanges()
            },
            onlyNumbers: function() {
               this.value = this.value.replace(/[^0-9.]/g,'');
            },
            onlyNumbersLetters: function() {
               this.valueRefString = this.valueRefString.replace(/[^\w]/gi,'');
            },            
            all(val) {
                this.value = val;
            },
            withdrawRef(){
                let _value = new BigNumber(this.value).times(10**6).toFixed()
                this.btnWithdrawRef = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).RefWithdraw(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at RefWithdraw', err)
                    }
                    console.log('RefWithdraw done!', Res)
                    GetBallance()
                    getRefBallance()
                    this.btnWithdrawRef = "dapp-btn";
                });
            },
            setRefAlias() {
                if (this.valueRefString == '') {
                    return;
                }
                let _value = this.valueRefString;
                this.btnSetRefAlias = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).setRefAlias(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at setRefAlias', err)
                    }
                    console.log('setRefAlias done!', Res)
                    GetBallance()
                    getRefAddressAlias()
                    this.btnSetRefAlias = "dapp-btn";
                });
            },
            withdraw() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnWithdraw = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).withdrawPLAY(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at withdraw HASH', err)
                    }
                    console.log('withdrawPLAY done!', Res)
                    getAccountTokensData()
                    GetBallance()
                    PLAYTokenBalance()
                    this.btnWithdraw = "dapp-btn";
                });
            },
            withdrawOS() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnWithdrawOS = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).withdrawOS(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at withdraw OS', err)
                    }
                    console.log('withdrawOS done!', Res)
                    getAccountTokensData()
                    GetBallance()
                    OSTokenBalance()
                    this.btnWithdrawOS = "dapp-btn";
                });
            },
            deposite() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnDeposite = "dapp-btn buzy";
                this.tronWeb.instance.contract(HASHTokenABI.abi, this.HashTokenAddress).approve(this.ContractAddress, _value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at approve HASH', err)
                    }
                    console.log('approve done!', 'Res', Res, 'this.ContractAddress', this.ContractAddress, '_value', _value)
                    this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).depositePLAY(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err1, Res1) => {
                        if (err1) {
                            console.log('Error at deposite HASH', err1)
                        }
                        console.log('depositePLAY done!', Res1)
                        GetBallance()
                        getAccountTokensData()
                        PLAYTokenBalance()
                        this.btnDeposite = "dapp-btn";
                    });
                });
            },
            depositeOS() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                console.log('_value', _value)
                this.btnDepositeOS = "dapp-btn buzy";
                this.tronWeb.instance.contract(OSTokenABI.abi, this.OSTokenAddress).approve(this.ContractAddress, _value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at approve OS', err)
                    }
                    console.log('approve done!', 'Res', Res, 'this.ContractAddress', this.ContractAddress, '_value', _value)
                    this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).depositeOS(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err1, Res1) => {
                        if (err1) {
                            console.log('Error at deposite OS', err1)
                        }
                        console.log('depositeOS done!', Res1)
                        GetBallance()
                        getAccountTokensData()
                        OSTokenBalance()
                        this.btnDepositeOS = "dapp-btn";
                    });
                });
            },
            freeze() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnFreeze = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).freezePLAY(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at freeze HASH', err)
                    }
                    console.log('freezePLAY done!', Res)
                    GetBallance()
                    getAccountTokensData()
                    frozenPLAY()
                    PLAYTokenBalance()
                    this.btnFreeze = "dapp-btn";
                });
            },
            freezeOS() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnFreezeOS = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).freezeOS(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at freeze OS', err)
                    }
                    console.log('freezeOS done!', Res)
                    GetBallance()
                    getAccountTokensData()
                    frozenOS()
                    OSTokenBalance()
                    this.btnFreezeOS = "dapp-btn";
                });
            },
            unfreeze() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnUnFreeze = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).unFreezePLAY(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at unfreeze HASH', err)
                    }
                    console.log('unFreezePLAY done!', Res)
                    GetBallance()
                    getAccountTokensData()
                    frozenPLAY()
                    PLAYTokenBalance()
                    this.btnUnFreeze = "dapp-btn";
                });
            },
            unfreezeOS() {
                let _value = new BigNumber(this.value).times(10**18).toFixed()
                this.btnUnFreezeOS = "dapp-btn buzy";
                this.tronWeb.instance.contract(ContractABI.abi, this.ContractAddress).unFreezeOS(_value).send({from: this.mainAddress, shouldPollResponse: true}, (err, Res) => {
                    if (err) {
                        console.log('Error at unfreeze OS', err)
                    }
                    console.log('unFreezeOS done!', Res)
                    GetBallance()
                    getAccountTokensData()
                    frozenOS()
                    OSTokenBalance()
                    this.btnUnFreezeOS = "dapp-btn";
                });
            }
        }
    }
</script>