// Get current reward state
const GetData = async function(){
    let tweb = await store.state.tronWeb.instance;
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).getData().call( (err, resData) => {
        if(err)
            return console.error('something wrong with getData()', err);
        console.log('roundNumber=', resData[0], 
            'ratio=', new BigNumber(resData[1]).dividedBy(10**18).toFixed(),
            'roundFrom=', new BigNumber(resData[2]).dividedBy(10**18).toFixed(),
            'roundTo=', new BigNumber(resData[3]).dividedBy(10**18).toFixed(),
            'sumTotal=', new BigNumber(resData[4]).dividedBy(10**6).toFixed());
        store.dispatch('writeRoundData', {
            roundNumber : resData[0],
            ratio       : new BigNumber(resData[1]).dividedBy(10**18).toFixed(),
            roundFrom   : new BigNumber(resData[2]).dividedBy(10**18).toFixed(),
            roundTo     : new BigNumber(resData[3]).dividedBy(10**18).toFixed(),
            sumTotal    : new BigNumber(resData[4]).dividedBy(10**6).toFixed()
        })
    })
}

