// get game pool balance
const PoolBalance = async function(){
    let tweb = await store.state.tronWeb.instance;
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).PoolBalance().call( (err, PoolBalance) => {
        if(err)
            return console.error('something wrong with PoolBalance()', err);        
        tweb.contract(riddleHashContractSource.abi, riddleHashContract).PoolBalancePeriodStart().call( (err, PoolBalancePeriodStart) => {
            if(err)
                return console.error('something wrong with PoolBalance()', err);
            
            let HASHdropBalance = 0
            if (BigNumber(PoolBalance).gte(PoolBalancePeriodStart)){
                HASHdropBalance = BigNumber(PoolBalance).minus(PoolBalancePeriodStart).dividedBy(10**6).dividedBy(4).toFixed()
            }
            console.log('(PoolBalance - PoolBalancePeriodStart) / 2', HASHdropBalance)
            store.dispatch('writeHASHDropBalance', HASHdropBalance)
        })
    })
}

