const showWin = async function (payload){
    store.dispatch('writeisWin', payload)
    await timeout(2000);
    store.dispatch('writeisWin', {isWin: false, winAmount: 0})
}

const GetBallance = async function () {
    console.log('Get Account then get Ballance starting!')
    let tweb = await store.state.tronWeb.instance
    let addr
    return new Promise((resolve) => {
        addr = tweb.defaultAddress.base58
        if (addr != false) {
            if (typeof addr !== 'undefined'){
                store.dispatch('ActionSetAddress', addr),
                resolve();
            }
        }
    })
    .then( () => {
        console.log('GetBallance starting! Address:', addr, '\n object ', tweb),
        tweb.trx.getBalance(addr, (err, balance) => {
            if(err)
                return console.error('something wrong', err);
            console.log('balance =', balance);
            store.dispatch('ActionSetTRXBal', balance)
        })
    })
}

const mathdiv = function (A, B) {
    return (A - A%B)/B}

const startEventListener = async function (tweb) {
    console.log('riddleHashContractSource.abi', riddleHashContractSource.abi, 'riddleHashContract', riddleHashContract)
    // Award watch events 
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).Award().watch((err, element) => {
        if(err) {
            console.log('Error while subscribe Award ', err)
        }
        if(element) {
            let res = element.result
            console.log('Award()', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, res.ActualTail, 
                tronWeb.fromSun(res.StakeValue), tronWeb.fromSun(res.Won), res.isWon)
            store.dispatch('writeStakeAward', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           parseInt(res.Won),
                _isWon:         res.isWon
            })
            
            store.dispatch('writeAllAwards', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           parseInt(res.Won),
                _isWon:         res.isWon
            })
            console.log('Award() end')
            GetBallance();
            if (res.isWon && parseInt(res.Won) > 0) {
                showWin({isWin: true, winAmount: parseInt(res.Won)});
            }
        }
    });
    // Award get history events
    tweb.getEventResult(riddleHashContract, { 
        eventName: 'Award', size: 250, page: 2
    }).then(result => {
        result.reverse().forEach(element => {
            let res = element.result
            console.log('Award read', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, res.ActualTail, tronWeb.fromSun(res.StakeValue), tronWeb.fromSun(res.Won), res.isWon)
            console.log('Award read2', store.state.mainAddress,tronWeb.address.fromHex(res.Address))
            if (store.state.mainAddress == tronWeb.address.fromHex(res.Address)) {
                store.dispatch('writeMyAwards', {
                    _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                    _BlockNumber:   parseInt(res.BlockNumber),
                    _from:          res.Address,
                    _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                    _StakeId:       res.StakeId,
                    _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                    _StakeValue:    parseInt(res.StakeValue),
                    _Won:           parseInt(res.Won),
                    _isWon:         res.isWon
                })

            } 
            store.dispatch('writeAllAwards', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           parseInt(res.Won),
                _isWon:         res.isWon
            })
        })
        GetBallance()
    })
    // Stake watch events 
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).staked().watch((err, element) => {
        if(err) {console.log('Error while subscribe Award ', err)}
        if(element) {
            let res = element.result
            console.log('Award()', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, tronWeb.fromSun(res.StakeValue))
            store.dispatch('writeMyAwards', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    '?',
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           0,
                _isWon:         null
            })
            GetBallance()
        }
    });
}

