<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container proof">
                    <div class="modal-header">
                        <div class="head3">Block number <font class="mark">{{blockNumber}} </font></div>
                        <div v-bind:class=" longhash === '0x0000000000000....0000' ? 'hide' : 'head3'">Block hash <font class="mark">{{longhash}}</font><font class="marklight">{{longhashtail}}</font></div>
                        <div class="head3">Block Hash <a v-bind:href="tronscanurl">proove HASH at tronscan...</a></div>
                    </div>
                    <div class="modal-body">
                    </div>
                    <div class="modal-footer">
                        <slot name="footer">
                            <button class="modal-default-button" @click="$emit('close')">Close</button>
                        </slot>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapState } from 'vuex';
    //import ContractABI from '../.././build/contracts/riddlehash.json';
    export default {
        computed:
            mapState({
                tronWeb: 'tronWeb',
                ContractAddress: 'riddleHashContractAddress',
            }),
        props: {
            awa: {
                blockNumber: Number,
                longhash: String
            }
        },
        data () {
            return {
                award: 0,
                blockNumber:0,
                longhash:'',
                longhashtail:'',
                tronscanurl:''
            }
        },
        mounted(){
            console.log('this.tronWeb.instance.trx', this.tronWeb.instance.trx);
            this.tronWeb.instance.trx.getBlockByNumber(this.awa.blockNumber, (err, Res) => {
                if (err) {
                    console.log('Error at getBlockByNumber', err)
                }
                console.log('blockNumber:', this.awa.blockNumber, 'Hash:', Res.blockID)
                this.blockNumber = this.awa.blockNumber;                
                this.longhash = Res.blockID.substring(0, 15) + '....' + Res.blockID.substring(Res.blockID.length-5, Res.blockID.length-1);
                this.longhashtail = (Res.blockID.substring(Res.blockID.length-1, Res.blockID.length)).toUpperCase();
                this.tronscanurl = 'https://shasta.tronscan.org/#/block/' + this.blockNumber;
            });
        },
    }
</script>