import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import { GetTronWeb, GetBallance, GetData, getAccountTokensData/* , GetAsset, blockToTime, LoanLastState  */} from './../util/getTronWeb.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: state,
  mutations: {
    registerTronWebInstance (state, payload) {
      console.log('committing mutation registerTronWebInstance', payload)
      state.tronWeb.instance = payload
      console.log('payload.ready=', payload.ready)
      state.tronWeb.connected = (payload.ready === true ? true : false)
      GetBallance()
      GetData()
      getAccountTokensData()
    },
    mutationSetAddress (state, payload) {
      state.mainAddress = payload
      state.mainAddressShort = payload.substring(0, 5) + '....' + payload.substring(payload.length-5)
    },
    MutationSetTRXBal (state, payload) {
      state.trxBallance = payload
      state.trxBallanceNice = payload / 10**6
    },
    MutationSetBlockHistory(state, payload) {
      (state.blocksHistory.length == 0 ? state.blocksHistory.push(payload) : {});
      if (state.blocksHistory[state.blocksHistory.length-1].Number != payload.Number)
      {
        if (state.blocksHistory.length > 3) {state.blocksHistory.shift()}
        state.blocksHistory.push(payload);
        //console.log('block#', payload.Number, 'tailHEX', payload.TailHex, 'TailDec', payload.TailDec, 'Hash', payload.Hash, 'HashShort', payload.HashShort)
        state.btnSelected = payload.TailHex;
      }
    },
    writeAllAwardsMutation(state, payload) {
      // add to state.awardHistory
      state.awardHistory.unshift({
      //state.awardHistory.push({
        '_betKeyStakeId': payload._betKeyStakeId,
        '_BlockNumber':   payload._BlockNumber,
        '_from':          payload._from,
        '_Prediction':    payload._Prediction,
        '_StakeId':       payload._StakeId,
        '_ActualTail':    payload._ActualTail,
        '_StakeValue':    payload._StakeValue,
        '_Won':           payload._Won,
        '_isWon':         payload._isWon
      })
      if (state.awardHistory.length > 40) {
        state.awardHistory.splice(41, 999999);
      }
    },
    writeMyAwardsMutation(state, payload) {
      // add to state.awardHistory
      state.awardHistoryMy.unshift({
      //state.awardHistory.push({
        '_betKeyStakeId': payload._betKeyStakeId,
        '_BlockNumber':   payload._BlockNumber,
        '_from':          payload._from,
        '_Prediction':    payload._Prediction,
        '_StakeId':       payload._StakeId,
        '_ActualTail':    payload._ActualTail,
        '_StakeValue':    payload._StakeValue,
        '_Won':           payload._Won,
        '_isWon':         payload._isWon
      })
      if (state.awardHistory.length > 40) {
        state.awardHistory.splice(41, 999999);
      }
    },
    writeStakeAwardMutation(state, payload) {
      state.awardHistoryMy.forEach(element => {
        if (element._betKeyStakeId == payload._betKeyStakeId) {
          element._ActualTail = payload._ActualTail;
          element._Won = payload._Won;
          element._isWon = payload._isWon;
        }
      });
    },
    writeRiddleHashContractAddressMutation(state, payload) {
      state.riddleHashContractAddress = payload;
    },
    writeHashTokenContractAddressMutation(state, payload) {
      state.HashTokenContractAddress = payload;
    },
    writeOSTokenContractAddressMutation(state, payload) {
      state.OSTokenContractAddress = payload;
    },
    writeStakeMutation(state, payload) {
      state.gameStakeNice = payload;
      state.gameStake = payload * 10**6;
    },
    writeRoundDataMutation(state, payload) {
      state.roundNumber = payload.roundNumber
      state.ratio       = payload.ratio
      state.roundFrom   = payload.roundFrom
      state.roundTo     = payload.roundTo
      state.sumTotal    = payload.sumTotal
    },
    writeTokensInfoMutation(state, payload) {
      state.frozenPLAY  = payload.frozenPLAY
      state.frozenOS    = payload.frozenOS
      state.PLAYdepo    = payload.PLAYdepo
      state.OSdepo      = payload.OSdepo
      state.frozenBlock = payload.frozenBlock
    },
    writeHASHDropBalanceMutation(state, payload) {
      state.HASHDropBalance = payload
    },
    writeTotalFrozenPLAYMutation(state, payload) {
      state.TotalFrozenPLAY = payload
    },
    writeTotalFrozenOSMutation(state, payload) {
      state.TotalFrozenOS = payload
    },
    writePLAYbalanceMutation(state, payload) {
      state.PLAYbalance = payload
    },
    writeOSbalanceMutation(state, payload) {
      state.OSbalance = payload
    },
    writerefPercentMutation(state, payload) {
      state.refPercent = payload
    },
    writeRefBallanceMutation(state, payload) {
      state.RefBallance = payload
    },
    writeRefAddressAliasMutation(state, payload) {
      state.RefAlias = payload
    },
    writeisWinMutation(state, payload) {
      state.isWin = payload.isWin
      state.winAmount = payload.winAmount
    }
  },
  actions: {
    registerTronWeb ({commit}) {
      console.log('start registerTronWebInstance')
      GetTronWeb.then(result => {
        console.log('registerTronWebInstance action result=', result, window.tronWeb)
        commit('registerTronWebInstance', result)
      }).catch(e => {
        console.log('registerTronWebInstance action error=', e)
      })
    },
    ActionSetAddress ({commit}, payload) {
      commit('mutationSetAddress', payload)
    },
    ActionSetTRXBal ({commit}, payload) {
      commit('MutationSetTRXBal', payload)
    },
    ActionSetBlockHistory ({commit}, payload) {
      commit('MutationSetBlockHistory', payload)
    },
    writeAllAwards({commit}, payload) {
      console.log('writeAllAwards started')
      commit('writeAllAwardsMutation', payload)
    },
    writeMyAwards({commit}, payload) {
      console.log('writeMyAwards started')
      commit('writeMyAwardsMutation', payload)
    },
    writeStakeAward({commit}, payload) {
      console.log('writeStakeAward started')
      commit('writeStakeAwardMutation', payload)
    },
    writeRiddleHashContractAddress({commit}, payload) {
      console.log('writeRiddleHashContractAddress started')
      commit('writeRiddleHashContractAddressMutation', payload)
    },
    writeHashTokenContractAddress({commit}, payload) {
      console.log('writeHashTokenContractAddress started')
      commit('writeHashTokenContractAddressMutation', payload)
    },
    writeOSTokenContractAddress({commit}, payload) {
      console.log('writeOSTokenContractAddress started')
      commit('writeOSTokenContractAddressMutation', payload)
    },    
    writeStake({commit}, payload) {
      console.log('writeStake started')
      commit('writeStakeMutation', payload)
    },
    writeRoundData({commit}, payload) {
      console.log('writeRoundData started')
      commit('writeRoundDataMutation', payload)
    },
    writeTokensInfo({commit}, payload) {
      console.log('writeTokensInfo started')
      commit('writeTokensInfoMutation', payload)
    },
    writeHASHDropBalance({commit}, payload) {
      console.log('writeHASHDropBalance started')
      commit('writeHASHDropBalanceMutation', payload)
    },
    writeTotalFrozenPLAY({commit}, payload) {
      console.log('writeTotalFrozenPLAYMutation started')
      commit('writeTotalFrozenPLAYMutation', payload)
    },
    writeTotalFrozenOS({commit}, payload) {
      console.log('writeTotalFrozenOSMutation started')
      commit('writeTotalFrozenOSMutation', payload)
    },
    writePLAYbalance({commit}, payload) {
      console.log('writePLAYbalanceMutation started')
      commit('writePLAYbalanceMutation', payload)
    },
    writeOSbalance({commit}, payload) {
      console.log('writeOSbalanceMutation started')
      commit('writeOSbalanceMutation', payload)
    },
    writerefPercent({commit}, payload) {
      console.log('writerefPercentMutation started')
      commit('writerefPercentMutation', payload)
    },
    writeRefBallance({commit}, payload) {
      console.log('writeRefBallanceMutation started')
      commit('writeRefBallanceMutation', payload)
    },
    writeRefAddressAlias({commit}, payload) {
      console.log('writeRefAddressAliasMutation started', payload)
      commit('writeRefAddressAliasMutation', payload)
    },
    writeisWin({commit}, payload) {
      console.log('writeisWinMutation started', payload)
      commit('writeisWinMutation', payload)
    }
  },
  modules: {
  }
})
